* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.container{
    display: flex;
    gap: 740px;
    padding: 37.54px 63px 0 0;
}
.back{
    width: 100%;
    height: 800px;
    background: url(../public/images/img1.png) no-repeat center center/cover;
}
.nav{
    display: flex;
    gap: 20px;
}
.logo a{
    text-decoration: none;
    color: white;
    font-size: 20px;
}
.nav a{
    text-decoration: none;
    color: white;
}
.sec{
    width: 100%;
    height: 800px;
    background: url(../public/images/img2.png) no-repeat center center/cover;
}
.third{
    width: 100%;
    height: 800px;
    background: url(../public/images/img3.png) no-repeat center center/cover;
}
.fourth{
    width: 100%;
    height: 800px;
    background: url(../public/images/img4.png) no-repeat center center/cover;
}
.fifth{
    width: 100%;
    height: 800px;
    background: url(../public/images/img5.png) no-repeat center center/cover;
}
.sixth{
    display: flex;
    flex-direction: column;
    background-color: #000;
    justify-content: center;
    align-items: center;
    padding-top: 120px;
    padding-bottom: 100px;
}
.sixth__title{
    width: 658px;
    height: 70px;

    font-weight: 400;
    font-size: 50px;
    line-height: 70px;

    text-align: center;

    color: #FFFFFF; 
}
.sixth__description{
    width: 277px;
    height: 38px;

    font-weight: 400;
    font-size: 23px;
    line-height: 38px;

    text-align: center;

    color: #FFFFFF;
}
.btn{
    width: 186px;
    height: 42px;

    background: #FFFFFF;
    border-radius: 30px;
}
.btn a{
    text-decoration: none;
    color: black;
}
.footer{
    background-color: #000;
    padding: 50px 130px 50px 120px;
    display: flex;
    gap: 232px;
}
.footer__left__inner{
    display: flex;
}
.footer__right__inner{
    display: flex;
    gap: 25px;
}

.footer__gap{
    height: 21px;

    font-weight: 400;
    font-size: 13px;
    line-height: 21px;
    
    color: #666666;
}
.footer__gap2{
    height: 28px;

    font-weight: 400;
    font-size: 13px;
    line-height: 28px;

    color: #666666;

}

::-webkit-scrollbar{
    width: 5px;
}
::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey; 
    border-radius: 5px;
  }
  ::-webkit-scrollbar-thumb {
    background: red; 
    border-radius: 8px;
  }